import React, { useRef } from "react";
import {
    withScriptjs,
    withGoogleMap,
    GoogleMap,
    Marker,
} from "react-google-maps";
import { Hotel } from "@cvccorp-components/icons";
import InfoBox from "react-google-maps/lib/components/addons/InfoBox";
import { Bubbly } from "./styles";
import "./styles.css";

const MapGroup = withScriptjs(
    withGoogleMap(
        ({ defaultData, data, zoom, active, handleSelect, onZoomChanged }) => {
            const mapRefs = useRef(null);

            const handleOnZoomChanged = () => {
                if (onZoomChanged) onZoomChanged(mapRefs.current);
            };

            return data ? (
                <>
                    <GoogleMap
                        defaultZoom={zoom}
                        defaultCenter={{
                            lat: defaultData.coordinates.latitude,
                            lng: defaultData.coordinates.longitude,
                        }}
                        options={{
                            streetViewControl: false,
                            fullscreenControl: false,
                            mapTypeControl: false,
                        }}
                        ref={mapRefs}
                        onZoomChanged={handleOnZoomChanged}
                    >
                        <Marker
                            name={data.id}
                            key={data.id}
                            position={{
                                lat: data.coordinates.latitude,
                                lng: data.coordinates.longitude,
                            }}
                            visible={false}
                        >
                            <InfoBox
                                options={{
                                    closeBoxURL: "",
                                    enableEventPropagation: true,
                                    boxStyle:  {
                                      overflow: `visible`
                                    },
                                }}
                            >
                                <Bubbly
                                    active={active === data.id}
                                    onClick={() => handleSelect(data)}
                                >
                                    <Hotel size="medium" />
                                </Bubbly>
                            </InfoBox>
                        </Marker>
                    </GoogleMap>
                </>
            ) : null;
        }
    )
);

export default MapGroup;
